import Vue from 'vue';
import VueRouter from 'vue-router';

import register from "../router/routers/register";
import login from "../router/routers/login";
import home from "../router/routers/home";
import order from "../router/routers/order";
import shopcart from "../router/routers/shopcart";
import my from "../router/routers/my";
import particulars from "../router/routers/particulars";
import address from "../router/routers/address";
import pay from "../router/routers/pay";
import forgot from "../router/routers/forgot";

Vue.use(VueRouter);

const routes = [
    
    // 注册
    register,
    // 登录
    login,
    // 首页
    home,
    // 订单
    order,
    // 购物车
    shopcart,
    // 我的
    ...my,
    // 详情页
    particulars,
    // 地址
    ...address,
    // 支付
    pay,
    // 找回密码
    ...forgot,
    
    {
        path: "*",
        redirect: {
            name: "Home",
        },
    },
    
]

const router = new VueRouter({
    // mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
