import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';

import VueAxios from 'vue-axios';
import axios from 'axios';

// 导入组件
import { 
    Button,
    NavBar,
    Form,
    Field,
    Tabbar,
    TabbarItem, 
    Stepper, 
    GoodsAction, 
    GoodsActionIcon, 
    GoodsActionButton, 
    Swipe, 
    SwipeItem,
    Lazyload,
    Empty,
    SwipeCell,
    Checkbox, 
    CheckboxGroup,
    SubmitBar,
    AddressEdit,
    AddressList,
    Popup,
    Tab, 
    Tabs,
    Icon,
    Uploader,
    Cell, 
    CellGroup,
    List,
    PullRefresh,
} from 'vant';

// rem 适配
import "lib-flexible";
// 字体图标库
import "font-awesome/css/font-awesome.min.css";
// swiper轮播图插件
// import "swiper/css/swiper.min.css";

Vue
    .use(Button)
    .use(NavBar)
    .use(Form)
    .use(Field)
    .use(Tabbar)
    .use(TabbarItem)
    .use(Stepper)
    .use(GoodsAction)
    .use(GoodsActionButton)
    .use(GoodsActionIcon)
    .use(Swipe)
    .use(SwipeItem)
    .use(Lazyload)
    .use(Empty)
    .use(SwipeCell)
    .use(Checkbox)
    .use(CheckboxGroup)
    .use(SubmitBar)
    .use(AddressEdit)
    .use(AddressList)
    .use(Popup)
    .use(Tab)
    .use(Tabs)
    .use(Icon)
    .use(Uploader)
    .use(Cell)
    .use(CellGroup)
    .use(List)
    .use(PullRefresh);

// 注册 axios
Vue.use(VueAxios, axios);

//配置请求基础路径
axios.defaults.baseURL = "http://www.kangliuyong.com:10002";

//配置post请求头
axios.defaults.headers.post['Content-Type'] = "application/x-www-form-urlencoded;charset=utf-8";

// 发起请求前处理
axios.interceptors.request.use(config => {

    if (config.method == "post") {
        let paramsString = "";
        // post请求参数序列化，转成一个字符串
        for (let key in config.data) {
            
            paramsString += key + "=" + config.data[key] + "&"
        }

        config.data = paramsString.slice(0, -1);
    }

    return config;
})

Vue.config.productionTip = false;

// 添加 appkey 属性保存vue原型上
Vue.prototype.appkey = "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=";

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
